.login-affiliate-container {
  width: 440px;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  padding: 10px 0;
  right: -13%;
  bottom: -55%;
  background: #000;
  z-index: 1500;
  animation: show 0.3s linear;
}
.login-affiliate-container::after {
  content: "";
  position: absolute;
  left: 10px;
  top: -20px;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 40px solid #000;
}

@keyframes show {
  from {
    transform: translateX(-500px);
  }
  50% {
    transform: translateX(-250px);
  }
  to {
    transform: translateX(0);
  }
}

.login-affiliate__body {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.login-affiliate__body .form-group {
  position: relative;
  width: 100%;
  padding: 0 30px;
  margin-bottom: 1.2rem;
}

.login-affiliate__body .form-group:first-child {
  margin-top: 10px;
}

.login-affiliate__footer {
  display: flex;
  justify-content: flex-start;
  padding: 0 30px;
  margin-bottom: 10px;
}

.login-affiliate__footer__login-btn {
  height: 50px;
}

.login-affiliate__footer__login-btn button {
  color: #fff;
  background: transparent;
  border: 0;
  outline: 0;
  font-size: 1.5rem;
  border-bottom: 2px solid transparent;
}
.login-affiliate__footer__login-btn button:hover {
  border-bottom: 2px solid greenyellow;
  font-weight: bold;
}

.login-affiliate__close {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: #000;
  border: 5px solid #000;
  top: -15px;
  right: -15px;
  width: 30px;
  height: 32px;
  border-radius: 50%;
  z-index: 2000;
  background: #fff;
  cursor: pointer;
  font-size: 19px;
  font-family: sans-serif;
  font-weight: bold;
  line-height: 1;
}

.login-affiliate__body label {
  margin: 0;
  color: #fff;
}

.login-affiliate__body input {
  border-bottom: 1px solid #fff;
  background: transparent;
  color: #fff;
}

.login-affiliate__body input:focus {
  background: transparent;
  border-bottom: 1px solid #fff;
  color: #fff;
}

.login-affiliate__body .validation-marketer {
  right: 31px;
  bottom: -23px;
  font-size: 15px;
}

@media (min-width: 992px) and (max-width: 1199px) {
  .login-affiliate-container{
    right: -20%;
    bottom: -55%;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .login-affiliate-container{
    right: -35%;
    bottom: -70%;
  }
}

@media (min-width: 576px) and (max-width: 767px) {
  .login-affiliate-container{
    right: -260px;
    bottom: -88%;
  }
}




