.DayPicker {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px auto;
}

.DayPicker-Caption {
  text-align: center;
  font-size: 1.2rem;
  color:#74c044;
  margin-bottom: 3em;
}

.DayPicker-wrapper {
  border: none;
  outline: none;
}

.DayPicker-Day {
  font-size: 1.7rem;
  outline: none;
  padding: 0.6em;
}

.DayPicker-Month {
  direction: rtl;
}

.DayPicker-Day:hover {
  cursor: pointer;
}

.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
  position: relative;
  background: linear-gradient(to right, #ebe84b, #74c044);
  color: #fff;
}

.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside):hover {
  background: linear-gradient(to right, #ebe84b, #74c044);
}

.DayPicker-Weekday {
  padding: 0.5em 1em;
  color: #0b0b0b;
  font-size: 30px;
}
.DayPicker-Weekdays {
  background: #ecf0f1;
}

.DayPicker-NavBar {
  position: absolute;
  top: 14px;
  right: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
}

.calendar-prev-button,
.calendar-next-button {
  font-size: 1.5rem;
  cursor: pointer;
  font-weight: bold;
}

.calendar-prev-button img,
.calendar-next-button img{
  height: 0.8em;
}

.react-datepicker-popper {
  z-index: 5001;
}

.react-datepicker-wrapper {
  display: block;
}

.react-datepicker__week, .react-datepicker__day-names {
  direction: rtl;
}
