/* popup complete */

.popup-general {
  border: 7px solid lightgreen;
  border-radius: 25px;
  display: flex;
  background: #fff;
  z-index: 6000;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  & .popup-close {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 2rem;
    cursor: pointer;
  }
}

.popup-complete-container {
  border: 7px solid lightgreen;
  border-radius: 25px;
  width: 500px;
  height: 280px;
  display: flex;
  background: #fff;

  & .popup-complete-content {
    width: 80%;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    &__header,
    &__footer {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      text-align: center;

      & h2 {
        margin: 0;
        padding: 0;
        font-weight: bold;
      }

      & span {
        line-height: 20px;
      }
    }

    &__footer button {
      background: grey;
      color: #fff;
      border-radius: 25px;
      padding: 5px 25px;
      outline: none;
      border: none;
      margin-top: 10px;
    }
  }

  & .popup-complete-image img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100px;
  }
  & .popup-complete-image {
    position: relative;
    width: 20%;
    height: 100%;
  }
}

/* popup fail */

.popup-fail-container {
  border: 7px solid lightgreen;
  border-radius: 25px;
  min-width: 450px;
  min-height: 250px;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  background: #fff;

  & .popup-close {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 2rem;
    cursor: pointer;
  }

  & .popup-fail-content__header,
  .popup-fail-content__footer {
    display: flex;
    flex-direction: column;
    width: max-content;
    text-align: center;
    justify-content: center;
    align-items: center;

    & button {
      background: grey;
      color: #fff;
      border-radius: 25px;
      padding: 5px 25px;
      outline: none;
      border: none;
      margin-top: 10px;
      width: max-content;
    }

    & h2 {
      margin: 0;
      padding: 0;
      font-weight: bold;
    }

    & span {
      line-height: 20px;
    }
  }
  .popup-fail-content__footer {
    margin-top: 20px;
  }
}

.popup-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
}

/* contact-from popup */

.popup-contact {
  width: 450px;
  height: 130px;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  & .popup-contact__header {
    h3 {
      font-weight: 700;
      color: #4c515b;
    }
  }

  & .popup-contact__text {
    span {
      font-size: 1.2rem;
      color: #4c515b;
      font-weight: 500;
    }
  }

  @media (max-width: 575px) {
    width: 300px;
    height: 110px;
  }
}

/* login-failed-popup */

.login-failed-popup {
  width: 420px;
  height: 130px;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__text {
    h3 {
      font-weight: bold;
      opacity: 0.9;
    }
  }

  &__button {
    button {
      width: 100px;
      height: 33px;
      font-size: 0.9rem;
      outline: none;
      border: 1px solid transparent;
      border-radius: 25px;
      background: grey;
      color: #fff;
    }

    button:active {
      border: 2px solid #000;
    }
  }

  @media (max-width: 575px) {
    width: 300px;
  }
}

/* user created popup */

.user-created-popup {
  width: 580px;
  height: 200px;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 5px;
    margin-top: 10px;

    h2 {
      color: #4c515b;
      font-weight: bold;
    }
    span {
      font-size: 1.1rem;
      color: #4c515b;
    }
  }

  &__text {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 27px;

    span {
      font-size: 1.1rem;
      color: #4c515b;
    }
  }
}

/* user exist popup */

.popup-user-exist {
  width: 600px;
  height: 120px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  span {
    font-size: 1.1rem;
    font-weight: bold;
    color: #4c515b;
  }

  @media (max-width: 575px) {
    width: 340px;
    height: 100px;

    span {
      margin-top: 10px;
      font-size: 0.7rem;
    }

    .popup-close {
      top: -2px;
      right: 8px;
      font-size: 1.5rem;
    }
  }

  @media (max-width: 320px) {
    width: 300px;

    span {
      margin-top: 10px;
      font-size: 0.6rem;
    }
  }
}
