.login-popup-general {
  border: 2px solid #cdcdcd;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 60px;
  right: 0;
  left: 0;
  bottom: 0;
  background: #fff;
  z-index: 1500;
  animation: show 0.3s linear;
}

.login-popup-general input {
  border-bottom: 1px solid #4c515b;
}

.login-popup-general input:focus {
  border-bottom: 2px solid #4c515b;
}

.login-form-container {
  width: 500px;
  height: 260px;
  justify-content: center;
}

.forgot-password-link {
  position: absolute;
  top: 95px;
  z-index: 10;
  left: 40px;
  font-size: 1.2rem;
  opacity: 0.5;
}

.login-form-container::after, .user-click-login-form::after, .change-password-login::after, .forgot-password-container::after {
  content: "";
  position: absolute;
  left: 10px;
  top: -20px;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 40px solid #fff;
}

@keyframes show {
  from {
    transform: translateX(-500px);
  }
  50% {
    transform: translateX(-250px);
  }
  to {
    transform: translateX(0);
  }
}

.login-form__body {
  display: flex;
  flex-direction: column;

  input::placeholder {
    font-size: 1.8rem;
    font-weight: normal;
    color: #4c515b;
  }

  .login-email-mg {
    margin-top: 15px;
  }
}

.login-form__body .form-group {
  position: relative;
  width: 100%;
  padding: 0 30px;
  margin-bottom: 1.2rem;
}

.login-form__body .form-group:first-child {
  margin-top: 10px;
}

.login-form__footer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  margin: 10px;
}
.login-form__footer__social-links,
.login-form__footer__login-btn,
.login-xs-span {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.login-form__footer__login-btn {
  grid-column: 1/2;
  border-right: 3px solid black;
}
.login-form__footer__login-btn button {
  background: transparent;
  border: 0;
  outline: 0;
  font-size: 1.5rem;
}

.login-form__footer__social-links {
  font-size: 1.5rem;
  font-weight: bold;
  grid-column: 2/3;
}
.login-form__footer__social-links div {
  text-align: center;
  width: 50%;
}
.login-form__footer__social-links i,
.login-form__footer__login-btn span {
  cursor: pointer;
}
.login-form__footer__social-links div:first-of-type {
  border-right: 2px solid #cdcdcd;
}

.login-form__close {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: #fff;
  top: -15px;
  right: -15px;
  width: 25px;
  height: 26px;
  border-radius: 50%;
  z-index: 2000;
  background: black;
  cursor: pointer;
  font-size: 19px;
  font-family: sans-serif;
  font-weight: bold;
  line-height: 1;
}

.login-form__body label {
  margin: 0;
}

.login-form__body .validation-marketer {
  right: 31px;
  bottom: -25px;
  font-size: 15px;
}

@media (min-width: 576px) and (max-width: 767px) {
  .login-form-container {
    width: 470px;
    left: -10px;
  }
}

@media (max-width: 575px) {
  .login-form-container {
    max-width: 350px;
    height: 200px;
    top: 40px;
    left: -30px;
  }

  .forgot-password-link {
    top: 85px;
    left: 35px;
    font-size: 0.9rem;
  }

  .login-form__body {
    margin: 10px 0;
  }

  .login-form__body input {
    font-size: 1rem;
    height: 2.5rem;
    border-bottom: 1px solid #4c515b;
    padding: 0;
  }

  .login-form__body input::placeholder {
    font-size: 1.5rem;
  }

  .login-form__footer {
    margin-bottom: 0;
  }

  .login-form__body .form-control {
    padding: 0;
  }

  .login-form__footer__login-btn {
    grid-column: 2/3;
    grid-row: 1/2;
    margin-right: 40px;
  }

  .login-form__footer__hidden-btn {
    grid-column: 1/2;
    grid-row: 1/2;
    margin-left: 40px;
  }

  .login-form__footer__hidden-btn button {
    background: transparent;
    border: none;
    outline: none;
  }

  .login-form__footer__login-btn button,
  .login-form__footer__hidden-btn button {
    font-size: 1.2rem;
  }

  .login-form__footer__login-btn,
  .login-form__footer__hidden-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    margin-bottom: 5px;
    border: none;
  }

  .login-form__footer__login-btn button:hover,
  .login-form__footer__hidden-btn button:hover {
    font-weight: bold;
    border-bottom: 1px solid green;
  }

  .login-form__body .form-group:first-child {
    margin-top: 0;
  }

  .login-form__body {
    margin: 0;
  }

  .login-form-container::after {
    left: 20px;
  }
}

@media (max-width: 320px) {
  .login-form-container {
    max-width: 305px;
  }
  .forgot-password-link {
    top: 90px;
  }
}

/* after login name click login*/

.user-click-login-form {
  width: 450px;
  height: 225px;
  align-items: flex-end;
  padding: 0 30px;
  cursor: initial;

  .user-click__username {
    margin-top: 20px;
    cursor: default;

    .user-click__username-link {
      cursor: pointer;
      text-shadow: 1px 0 rgba(0,0,0,0.5);
    }
  }

  button {
    margin-top: 20px;
    outline: none;
    border: none;
    border-bottom: 2px solid green;
    background: transparent;
  }
  @media (max-width: 575px) {
    max-width: 350px;
    height: 165px;
    top: 40px;
    left: -30px;

    .login-form__close {
      width: 18px;
      height: 18px;
      font-size: 12px;
      top: -12px;
      right: -10px;
    }
  }
  @media (max-width: 320px) {
      max-width: 300px;
  }
}

/* change password */

.change-password-login {
  width: 500px;
  height: 330px;
  padding: 0 40px;
  cursor: initial;

  &__header {
    text-align: right;
    margin-top: 25px;

    span {
      font-size: 1.6rem;
      cursor: default;
    }
  }

  &__inputs {
    input {
      margin-top: 30px;
    }

    .change-password__rules {
      position: absolute;
      left: -20px;
      top: 0;
      width: 150px;
      white-space: normal;
      font-size: 13px;
      opacity: 0.6;
      font-weight: bold;
      pointer-events: none;
      line-height: 17px;
    }

    input::placeholder {
      font-size: 1.6rem;
      font-weight: normal;
      color: #4c515b;
    }

    .change-password__validation-result {
      position: absolute;
      right: 5px;
      bottom: -40px;
      font-size: 16px;
      opacity: 0.7;
      cursor: default;

      i {
        margin-left: 5px;
      }
    }
  }

  &__footer {
    margin-top: 55px;
    button {
      outline: none;
      border: none;
      border-bottom: 2px solid green;
      background: transparent;
    }
  }

  @media (max-width: 575px) {
    max-width: 350px;
    height: 265px;
    top: 40px;
    left: -30px;

    .change-password-login__header {
      margin-top: 15px;
    }

    .change-password-login__inputs {
      input {
        margin-top: 20px;
      }

      input::placeholder {
        font-size: 1.3rem;
      }

      .change-password__rules {
        left: -5px;
        width: 96px;
        top: -5px;
        font-size: 10px;
        line-height: 13px;
      }

      .change-password__validation-result {
        right: 1px;
        bottom: -28px;
        font-size: 13px;
      }
    }

    .change-password-login__footer {
      margin-top: 40px;

      button {
        font-size: 1.2rem;
      }
    }

    .login-form__close {
      width: 18px;
      height: 18px;
      font-size: 12px;
      top: -12px;
      right: -10px;
    }
  }
  @media (max-width: 320px) {
    max-width: 300px;
    height: 255px;

    .change-password-login__inputs {
      input {
        margin-top: 15px;
      }

      input::placeholder {
        font-size: 1.1rem !important;
      }

      .change-password__rules {
        left: -12px;
        width: 85px;
        font-size: 0.7rem;
        line-height: 10px;
        top: 3px;
      }

      .change-password__validation-result {
        bottom: -26px;
        font-size: 12px;
      }
    }
  }
}

/* forgot password */

.forgot-password-container {
  width: 450px;
  height: 250px;
  padding: 0 40px;
  cursor: default;

  .forgot-password-input {
    margin-top: 50px;

    input::placeholder {
      font-size: 1.6rem;
      color: #4c515b;
      font-weight: 500;
    }

    &__validation-result {
      position: absolute;
      font-size: 1rem;
      opacity: 0.6;
      right: 5px;
      bottom: -35px;
    }

    &__validation-result i {
      margin-left: 5px;
    }
  }
  .forgot-password-button {
    position: relative;
    bottom: -60px;

    button {
      outline: none;
      border: none;
      border-bottom: 2px solid green;
      background: transparent;
      font-weight: 700;
    }
  }

  @media (max-width: 575px) {
    max-width: 350px;
    height: 180px;
    padding: 0 30px;
    top: 40px;
    left: -30px;

    .forgot-password-input {
      margin-top: 25px;

      ::placeholder {
        font-size: 1.3rem !important;
      }

      &__validation-result {
        font-size: 0.8rem;
        right: 2px;
        bottom: -30px;
      }
    }
    .login-form__close {
      width: 18px;
      height: 18px;
      font-size: 12px;
      top: -12px;
      right: -10px;
    }
    .forgot-password-button {
      bottom: -40px;

      button {
        font-size: 1.2rem;
      }
    }
  }
  @media (max-width: 320px) {
    max-width: 300px;
    padding: 0 15px;
    height: 180px;

    .forgot-password-input {

      &__validation-result {
        font-size: 0.7rem;
      }
    }
  }
}

