  height: 20px;
  margin: 0 10px 10px 0;
  transition: 0.2s ease;
}

/* event hall */

.circle-stats {
  display: flex;
  margin: 40px 0 100px 0;
}

.circle-stat-item {
  width: 150px;
  height: 150px;
  background: #cdcdcd;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
}

.big-circle-stat-item {
  width: 200px;
  height: 200px;
  background: linear-gradient(to left, #d6e046, #73bf44);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circle-stats-right,
.circle-stats-left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.circle-stats-right h3,
.circle-stats-left h3 {
  margin-top: 20px;
  font-size: 20px;
}
.affiliate-personal {
  margin-top: 80px;
}

.event-affiliate-list, .marketer-stat-container {
  height: 480px;
  overflow: scroll;
  overflow-x: hidden;
  padding-right: 70px;
}

.event-affiliate-list::-webkit-scrollbar, .marketer-stat-container::-webkit-scrollbar {
  width: 10px;
}

.event-affiliate-list::-webkit-scrollbar-track, .marketer-stat-container::-webkit-scrollbar-track {
  background: linear-gradient(
    to left,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0) 30%,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 0) 70%,
    rgba(0, 0, 0, 0) 100%
  );
}

.event-affiliate-list::-webkit-scrollbar-thumb, .marketer-stat-container::-webkit-scrollbar-thumb {
  background: linear-gradient(to top, #d6e046, #73bf44);
  border-radius: 15px;
}

.event-icons {
  margin-bottom: 100px;
  margin-top: 65px;
}

.total-prize h2 {
  font-size: 3.5rem;
}

.event-icons img {
  width: 80px;
  height: 80px;
  cursor: pointer;
}

.event-icons h3 {
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

.event-affiliate-row {
  border-bottom: 3px solid #4c515b;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
}

.in-input {
  position: absolute;
  top: 15px;
  left: 170px;
  font-size: 2rem;
  color: #4c515b;
  cursor: pointer;
}
.marketer-stat, .marketer-stat-header {
  border-bottom: 3px solid #4c515b;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.3rem;
}
.stat-info {
  display: flex;
}
.stat-info div {
  width: 100%;
  display: flex;
  align-items: center;
}
.stat-info div:nth-child(1) {
  justify-content: center;
}
.stat-info div:nth-child(2) {
  justify-content: center;
}
.stat-info div:nth-child(3) {
  justify-content: flex-end;
  padding: 0;
}
.no-pr {
  padding-right: 0 !important;
}
.marketer-stat-header {
  padding-right: 70px;
  border-bottom: none !important;
}
.marketer-stat-header h3 {
  font-size: 1.4rem;
}
.marketer-stat:first-of-type {
  border-top: 3px solid #4c515b;
}
.marketer-stat-container {
  margin-bottom: 100px;
}

.bordered-header {
  border-left: 3px solid #4c515b;
  border-right: 3px solid #4c515b;
}

.event-affiliate-buttons {
  display: flex;
  justify-content: space-between;
}

.pure-btn,
.green-btn {
  padding: 4px 5px;
  border-radius: 20px;
  outline: none !important;
}
.pure-btn {
  border-radius: 15px;
  border: 2px solid #4c515b;
}
.pure-btn:active {
  border: 2px solid black;
}

.green-btn {
  background: linear-gradient(to left, #d6e046, #73bf44);
  color: #fff;
}

.mark-btn {
  padding: 4px 10px;
  border-radius: 15px;
  outline: none !important;
  font-size: 1.2rem;
}
.mark-btn:active {
  border: 2px solid black;
}

.new-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  font-size: 20px;
}

.new-pagination img {
  width: 15px;
  height: 20px;
}

.new-pagination li {
  padding: 5px 10px;
  font-size: 25px;
}
.new-pagination-active {
  text-decoration: underline;
  font-weight: bold;
  color: #0b2e13;
}

.new-pagination li a {
  color: #73bf44;
  text-decoration: none;
}
.event-agree, .form-agree {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap-reverse;
}
.form-agree-marketer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap-reverse;
  margin-top: 40px;
}

.type-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
  direction: rtl;
  position: relative;
}

.type-select option {
  direction: rtl;
}

.add-file .form-group {
  padding: 10px 10px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.add-file .form-group.validation-on {
  bottom: 20px;
}

.add-file input[type="file"] {
  outline: 0;
  opacity: 0;
  display: none;
  pointer-events: none;
  user-select: none;
}

.add-file .label {
  width: 90px;
  height: 90px;
  border-radius: 15px;
  border: 2px solid #939598;
  transition: border 300ms ease;
  cursor: pointer;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-file .label img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.add-file .label i {
  display: block;
  font-size: 42px;
}

.add-file .label i,
.example-1 .label .title {
  color: #939598;
  transition: 200ms color;
}

.add-file-not-found i {
  color: red !important;
}
.add-file-not-found label {
  border-color: red !important;
}
.mistake-label-camera {
  color: red !important;
}

.add-file:hover i {
  color: #000;
}
.add-file:hover .label {
  border: 2px solid #000;
}

.img-logo {
  background: url("./logo.png") center no-repeat;
  background-size: 20px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 3px solid #4c515b
}
.img-arrow {
  background: url("./arrow.png") center no-repeat;
  background-size: 25px;
  width: 40px;
  height: 40px;
  float: left;
  transition: 0.2s ease;
}

.img-arrow-marketer, .img-arrow-marketer-top, .img-arrow-marketer-search {
  pointer-events: none;
  background: url("./arrow.png") center no-repeat;
  background-size: 25px;
  width: 40px;
  height: 40px;
  position: absolute;
  left: 0;
  z-index: 5000;
  transition: 0.2s ease;
}

.img-arrow-marketer {
  bottom: 5px;
}

.img-arrow-marketer-top {
  bottom: 25px;
}

.img-arrow-marketer-search {
  bottom: 5px;
}

.password-container, .bank-data-container, .user-payment-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#user-payment .from-wrapper {
  width: 33% !important;
}

.contact-form-username {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.contact-form-username .form-group {
  width: 50%;
}

.form-group .contact-submit {
  width: 100px;
  border-radius: 15px;
}

.contact-form {
  width: 100%;
  padding: 0 100px;
}
.contact-form .validation-marketer {
  right: 0;
}
.contact-form label {
  margin-bottom: 0;
}
.contact-footer {
  width: 100%;
}
.contact-footer .form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.contact-footer .form-group button {
  margin: 0 auto !important;
}

.video-audio-congratulations {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
}

.add-file-image input[type="file"] {
  outline: 0;
  opacity: 0;
  display: none;
  pointer-events: none;
  user-select: none;
}


.add-file-image img{
  cursor: pointer;
  width: 100%;
  height: 100%;
}


.picture-md {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  margin: 150px 0;
}

.picture-md img {
  cursor: pointer;
  transform: scale(1.25);
}
.btn-ev {
  display: flex;
  align-items: center;
}

.video, .audio, .picture{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 33%;
}

.video img, .audio img, .picture img{
  width: 100%;
  height: 100%;
}

.from-wrapper-marketer {
  width: 100%;
  float: right;
}
.marketer-input-container {
  display: flex;
  flex-direction: row-reverse;
}
.captcha {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}
.camera-block {
  margin-bottom: 40px;
}

.payment-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.payment-title {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}

.money-present {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 4rem;
  background: #cdcdcd;
  font-size: 1.3rem;
}
.money-present div {
  width: 33%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  height: 1.3rem;
}

.money-present div:nth-child(2) {
  border-right: 1px solid black;
  border-left: 1px solid black;
}

.signment .event-agree input[type="checkbox"] {
  width: 20px;
  height: 20px;
}

.bank-data {
  min-height: 80px;
}
.bank-data-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bank-data-position {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 70px 0 50px;
}

.bank-data__content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  margin-right: 30px;
  font-size: 1.4rem;
}

.bank-data__content-header {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  text-align: right;
  white-space: nowrap;
}
.bank-data__content-body {
  width: 100%;
}
.bank-data__content-body ul {
  display: flex;
  justify-content: flex-end;
}
.bank-data__content-body ul li {
  list-style: none;
  white-space: nowrap;
}
.bank-data__content-body ul li:nth-child(2) {
  margin:0 5px;
}
.bank-data__image {
  display: flex;
  justify-content: center;
  align-items: center;
}
.bank-data__button {
  border-radius: 10px;
  border: 2px solid black;
  background: transparent;
  white-space: nowrap;
}
.bank-data__image img {
  width: 160px;
  height: 160px;
  margin-bottom: 20px;
}

.contact-error .validation-marketer {
  bottom: -5px;
}

.new-password-rules {
  position: absolute;
  left: 10px;
  bottom: 10px;
  width: 120px;
  white-space: normal;
  font-size: 13px;
  opacity: 0.6;
  font-weight: bold;
  pointer-events: none;
  line-height: 17px
}

.new-password-rules-active {
  bottom: 34px;
}

.rules-was-not-read .validation-marketer{
  right: 15px;
  bottom: -20px;
}

/* event hall download page component */

.eh-download-page, .eh-download-page__descr {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.eh-download-page__descr span {
  font-size: 30px;
  color: #4c515b;
  text-align: center;
}

.eh-download-page a {
  width: 320px;
  height: 55px;
  background: #2c3e50;
  border-radius: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  text-decoration: none;
}

.eh-download-page a span{
  font-size: 1.5rem;
  color: #fff;
  margin-top: 5px;
}

.eh-download-page a img {
  margin-left: 15px;
  height: 1em;
  transform: scale(3);
}

/* events not found */

.events-not-found-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
}

.events-not-found-container h2 {
  color: red;
}

.bank-wrong .validation-marketer, .branch-wrong .validation-marketer {
  bottom: -5px;
  right: 10px;
}




